.description-container-item-image {
    width: 350px;
    height: 350px;
    background-color: #ffffff;
    box-shadow: 0px 0px 20px rgba(0, 0, 0, 0.1);
    border-radius: 30px;
    margin-left: 20px;
}

.description-container {
    width: 1110px;
    display: flex;
    flex-wrap: wrap;
}

.description-item-image {
    display: table-cell;
    height: 350px;
    text-align: center;
    width: 350px;
    vertical-align: middle;
}

.description-container {
    display: flex;
}

.description-container-item-title {
    width: 700px;
    height: 40px;
    background-color: #ffffff;
    box-shadow: 0px 0px 20px rgba(0, 0, 0, 0.1);
    border-radius: 30px;
    font-family: 'Montserrat';
    font-style: normal;
    font-weight: 600;
    font-size: 20px;
    line-height: 30px;
    display: flex;
    justify-content: center;
    align-items: center;
}

.title-specification-container {
    width: 700px;
    margin-left: auto;
    margin-right: auto;
}

.description-specifications {
    display: flex;
    flex-wrap: wrap;
    margin-top: 20px;
    width: 700px;
    height: 290px;
    background-color: #ffffff;
    box-shadow: 0px 0px 20px rgba(0, 0, 0, 0.1);
    border-radius: 30px;
    font-family: 'Montserrat';
    font-style: normal;
    font-weight: 600;
    font-size: 20px;
    line-height: 30px;
}
.description-specifications-title {
    width: 700px;
    text-align: center;
    margin-top: 10px;
}

.description-brief-characteristics {
    width: 700px;
    height: 180px;
    display: flex;
    justify-content: center;
}

.description-rus-characteristics {
    font-family: 'Montserrat';
    font-style: normal;
    font-weight: 600;
    font-size: 16px;
    line-height: 30px;
    width: 250px;
    display: flex;
    flex-direction: column;
}
.description-characteristics {
    font-family: 'Montserrat';
    font-style: normal;
    font-weight: 600;
    font-size: 16px;
    line-height: 30px;
    width: 250px;
    display: flex;
    flex-direction: column;
    margin-left: 20px;
}

.description_all_characteristics {
    font-family: 'Montserrat';
    font-style: normal;
    font-weight: 600;
    font-size: 18px;
    line-height: 30px;
    margin: auto;
    border: none;
    outline: none;
    background-color: #ffffff;
    cursor: pointer;
}

.description_all_characteristics:hover {
    color: #838383;
    text-decoration: underline;
}

.about-product-favorite-buy-container {
    width: 1070px;
    background-color: #ffffff;
    box-shadow: 0px 0px 20px rgba(0, 0, 0, 0.1);
    border-radius: 30px;
    margin: auto;
    margin-top: 30px;
    display: flex;
    justify-content: center;
    margin-bottom: 20px;
}

.about-product-container {
    width: 550px;
    margin-bottom: 20px;
    margin-top: 30px;
}
.about-product-title {
    font-family: 'Montserrat';
    font-style: normal;
    font-weight: 600;
    font-size: 18px;
    line-height: 30px;
    margin-bottom: 20px;
}

.description-about-product {
    margin-bottom: 15px;
}

.favorite-buy-container {
    margin-left: 30px;
    width: 400px;
    margin-top: 30px;
}

.description-price-container {
    width: 250px;
    height: 100px;
    border-radius: 20px;
    border: 2px solid #d4d4d4;
    margin: auto;
    margin-top: 20px;
}

.description-with-discount-container {
    width: 100%;
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
}

.description-with-discount {
    display: flex;
    align-items: center;
}

.description-headphone-discount {
    font-family: 'Montserrat';
    font-style: normal;
    font-weight: 600;
    font-size: 18px;
    line-height: 20px;
    color: #df6464;
    margin-left: 20px;
}

.description-price-with-discount {
    text-align: center;
    font-family: 'Montserrat';
    font-style: normal;
    font-weight: 600;
    font-size: 25px;
    line-height: 25px;
    color: #df6464;
}

.description-headphone-price {
    font-family: 'Montserrat';
    font-style: normal;
    font-weight: 600;
    font-size: 25px;
    line-height: 30px;
    color: #ffa542;
}

.description-headphone-old-price {
    font-family: 'Montserrat';
    font-style: normal;
    font-weight: 600;
    font-size: 17px;
    line-height: 16px;
    text-decoration-line: line-through;
    color: #ffce7f;
}

.description-place-order {
    cursor: pointer;
    width: 150px;
    height: 45px;
    background: #101010;
    box-shadow: 0px 0px 20px rgba(0, 0, 0, 0.25);
    border-radius: 20px;
    margin-left: 15px;
}

.description-buy-favorite {
    display: flex;
    align-items: center;
    width: 400px;
    margin-top: 20px;
    justify-content: center;
}
