@import url('https://fonts.googleapis.com/css?family=Montserrat&display=swap');
.footer {
    display: flex;
    height: 149px;
    max-width: 1110px;
    min-width: 430px;
    padding: 0;
    margin: auto;

    background: #ffffff;
    box-shadow: 0px 0px 20px rgba(0, 0, 0, 0.1);
    border-radius: 30px 30px 0px 0px;
    justify-content: space-between;
}
.footer .title-site {
    padding-top: 29px;
    padding-left: 29px;
    font-family: 'Montserrat';
    font-style: normal;
    font-weight: 700;
    font-size: 25px;
    line-height: 30px;
    color: #101010;
    text-decoration: none;
}

.link-nav {
    text-decoration: none;
}

.other-pages {
    width: 100px;
    display: flex;
    flex-direction: column;
}

.favourites {
    margin-top: 34px;
    text-decoration: none;
    font-family: 'Montserrat';
    font-style: normal;
    font-weight: 400;
    font-size: 17px;
    line-height: 21px;
    color: #000000;
}
.cart {
    margin-top: 11px;
    text-decoration: none;
    font-family: 'Montserrat';
    font-style: normal;
    font-weight: 400;
    font-size: 17px;
    line-height: 21px;
    color: #000000;
}
.contacts {
    margin-top: 10px;
    text-decoration: none;
    font-family: 'Montserrat';
    font-style: normal;
    font-weight: 400;
    font-size: 17px;
    line-height: 21px;
    color: #000000;
}

.service-footer {
    width: 160px;
    display: block;
    padding-top: 35px;
}
.language {
    width: 100px;
    display: flex;
    align-items: center;
    justify-content: space-between;
}

.terms-service {
    text-decoration: none;
    font-family: 'Montserrat';
    font-style: normal;
    font-weight: 400;
    font-size: 17px;
    line-height: 21px;
    color: #000000;
    height: 40px;
}

.rus-language {
    margin-left: 20px;
    font-family: 'Montserrat';
    font-style: normal;
    font-weight: 700;
    font-size: 15px;
    line-height: 18px;
    text-decoration: none;
    color: #ffa542;
}
.eng-language {
    font-family: 'Montserrat';
    font-style: normal;
    font-weight: 500;
    font-size: 15px;
    line-height: 18px;
    color: #101010;
    text-decoration: none;
    margin-left: 20px;
}

.social-networks {
    height: 30px;
    width: 129px;
    margin-top: 29px;
    display: flex;
    justify-content: space-between;
    padding-right: 50px;
}
